.burger {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  user-select: none;
  cursor: pointer;
}

.burger > span {
  display: inline-block;
  width: 100%;
  height: 3px;
  background: #000;
  position: relative;
  transition: background 0.3s 0.3s;
}

.burger > span:before, .burger > span:after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background: #000;
  transform-origin: 50% 50%;
  transition: top 0.3s 0.3s, transform 0.3s;
}

.burger > span:before {
  top: -10px;
}

.burger > span:after {
  top: 10px;
}

.burger--close > span {
  transition: background 0.3s 0s;
  background: transparent;
}

.burger--close > span:before, .burger--close > span:after {
  transition: top 0.3s, transform 0.3s 0.3s;
  top: 0;
}

.burger--close > span:before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.burger--close > span:after {
  transform: rotate3d(0, 0, 1, -45deg);
}

.close {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  user-select: none;
  cursor: pointer;
}

.close > span {
  display: inline-block;
  width: 100%;
  height: 2px;
  background: transparent;
  position: relative;
}

.close > span:before, .close > span:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #000;
  transform-origin: 50% 50%;
}

.close > span:before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.close > span:after {
  transform: rotate3d(0, 0, 1, -45deg);
}

